@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

   .App {
     font-family: 'Inter', sans-serif;
     background-color: #1a1a2e;
     color: #ffffff;
     margin: 0;
     padding: 0;
     position: relative;
     min-height: 100vh;
   }

   .particles-container {
     position: fixed;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     z-index: 1000; /* Increased to ensure it's above other elements */
     pointer-events: auto;
   }

   #particles-js {
     position: absolute !important;
     top: 0;
     left: 0;
     width: 100% !important;
     height: 100% !important;
     z-index: 0 !important;
     pointer-events: auto !important;
   }

   .particles-fallback {
     color: #ffffff;
     text-align: center;
     padding: 20px;
   }

   .back-to-top {
     position: fixed;
     bottom: 20px;
     right: 20px;
     background: linear-gradient(45deg, #a3a3ff, #00aaff);
     color: #ffffff;
     border: none;
     border-radius: 50%;
     width: 50px;
     height: 50px;
     font-size: 24px;
     display: flex;
     align-items: center;
     justify-content: center;
     cursor: pointer;
     box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
     transition: transform 0.3s ease, opacity 0.3s ease;
     z-index: 1000;
   }

   .back-to-top:hover {
     transform: scale(1.1);
     opacity: 0.9;
   }

   h1, h2, h3, h4, h5, h6 {
     color: #ffffff;
   }

   p, a, span {
     color: #d0d0d0;
   }

   h1 {
     font-size: clamp(2rem, 5vw, 3rem);
   }

   h2 {
     font-size: clamp(1.5rem, 4vw, 2rem);
   }

   p {
     font-size: clamp(0.9rem, 2vw, 1rem);
   }